
import { FaceSDK } from "./facesdk";
import {Base64} from 'js-base64'

(async () => {

  // init OpenCV and open webcam
  let facesdk = new FaceSDK()

  async function buttonClick() {
    let btn = document.getElementById("btnCapture")
    btn.disabled = true

    let img = facesdk.captureImage();

    let response = await fetch('http://localhost:5000/api/wv_compute_matching_score_two_faces', {method: 'POST', headers: {ContentType: 'application/json'},  body: JSON.stringify({width:img.size().width, height: img.size().height, image: Base64.fromUint8Array(img.data), user_id: Math.floor(Date.now()/500) - 3205503000})});
    let data = await response.json();
    console.log(JSON.stringify(data));

    alert(JSON.stringify(data));


    img.delete();

    btn.disabled = false
  }
  
  facesdk.loadOpencv(() => {

    let mask = document.getElementById("img_face_frame");
    mask.hidden = false;

    let id_rect = document.getElementById("img_id_frame");
    id_rect.hidden = false;

    facesdk.startCamera('hd', 'videoInput', () => {
      let btn = document.getElementById("btnCapture")
      btn.disabled = false
      btn.addEventListener("click", (e:Event) =>  buttonClick());

      facesdk.handleVideo()
    })
  })

  
})()
